export default textLoop => {
    const windowWidth = window.innerWidth;
    const headbandInner = textLoop.querySelector('.text-loop');
    const headbandNews = textLoop.querySelector('.loop');
    let headbandNews__width = headbandNews.offsetWidth;

    while (headbandNews__width < (windowWidth * 2)) {
        const clone = headbandNews.cloneNode(true);
        headbandInner.appendChild(clone);
        headbandNews__width += headbandNews.offsetWidth;
    }

    headbandInner.classList.add('start');
};
